import React from 'react';
import { BrowserRouter as Router, Route, Switch, Link } from 'react-router-dom';
import VideoList from './components/VideoList';
import SoundCloudList from './components/SoundCloudList';

function App() {
  return (
    <Router>
      <div className="app">
        <nav className="navbar">
          <div className="navbar-brand">ProdByPrimeTime</div>
          <div className="navbar-links">
            <Link to="/" className="nav-link">YouTube</Link>
            <Link to="/soundcloud" className="nav-link">SoundCloud</Link>
          </div>
        </nav>
        <main className="main-content">
          <Switch>
            <Route exact path="/" component={VideoList} />
            <Route path="/soundcloud" component={SoundCloudList} />
          </Switch>
        </main>
      </div>
    </Router>
  );
}

export default App;