import axios from 'axios';

const API_KEY = process.env.REACT_APP_YOUTUBE_API_KEY;
const CHANNEL_ID = 'UCgJt5XKYzrqNVMm6QRHm4ZA';

export const fetchVideos = async () => {
  if (!API_KEY) {
    throw new Error('YouTube API key is not configured. Please set REACT_APP_YOUTUBE_API_KEY in your environment variables.');
  }
  const response = await axios.get('https://www.googleapis.com/youtube/v3/search', {
    params: {
      part: 'snippet',
      channelId: CHANNEL_ID,
      maxResults: 50,
      type: 'video',
      key: API_KEY,
      order: 'date'
    },
  });
  console.log('Fetched videos:', response.data.items); // Debug log
  return response.data.items;
};